import { mapMutations, mapState, mapActions } from 'vuex';
import articleList from '@/components/articleList.vue';
export default {
	data() {
		return {
			banner: [{ img: require('../../static/banner.png') }],
			zizhi: [],
			type: 1,
			more: false,
			pageNum: 1,
			pageSize: 10,
			loading: true,
			list: []
		};
	},
	computed: {
		...mapState(['isLogin', 'userInfo', 'city', 'uList', 'msgNum', 'count'])
	},
	components: { articleList },
	onShareAppMessage() {
		let token = uni.getStorageSync('TOKEN');
		let user = uni.getStorageSync('userInfo');
		if (token) {
			return {
				title: '易建联共享服务平台',
				path: '/pages/index/index?server=' + user.server_people_id
			};
		}
	},
	onLoad(opt) {
		this.$Fetch({
			url: 'picture/getData',
			success: res => {
				this.zizhi = res.picture;
				this.banner = res.banner;
			}
		});
		this.getList();
		if (opt.server) {
			this.setShareMsg({ server: opt.server });
		}
		let that = this;
		const token = uni.getStorageSync('TOKEN');
		if (token) {
			this.$Fetch({
				url: 'user/getDataById',
				success: res => {
					this.setInfo(res);
					uni.setStorageSync('userInfo', res);

					// uni.connectSocket({
					// 	url: 'wss://47.105.172.53:8282'
					// });
					uni.connectSocket({
						url: 'wss://yjl.tao.li4.cn:8282'
					});
					uni.onSocketOpen(function(ret) {
						console.log('WebSocket连接已打开！');
						uni.sendSocketMessage({
							data: JSON.stringify({
								type: 'bind',
								user_id: res.id
							}),
							success: function() {
								console.log('发送成功');
							}
						});
						uni.onSocketMessage(function(ret) {
							that.$Fetch({
								url: 'user_chat/getDataList',
								success: res => {
									that.updateUlist(res);
								}
							});
							let msg = JSON.parse(ret.data);
							uni.$emit('onMessage', msg);
							if (msg.type === 'text') {
								that.setMsgNum(that.msgNum + 1);

								let uList = that.uList;
								for (let item of uList) {
									if (item.id === msg.user_id) {
										if (item.count) {
											item.count++;
										} else {
											item.count = 1;
										}
									}
								}
								that.updateUlist(uList);






















							}
						});
					});
					uni.onSocketClose(function(ret) {
						console.log('WebSocket 已关闭！');
					});
					uni.onSocketError(function(ret) {
						uni.showToast({
							title: '服务器连接异常！重连中···',
							icon: 'none'
						});
						console.log('服务器连接异常！重连中···');
						uni.connectSocket({
							url: 'wss://yjl.tao.li4.cn:8282'
						});
					});

					this.$Fetch({
						url: 'user_chat/NoReadNum',
						success: res => {
							console.log('未读消息', res);
							this.setMsgNum(res);
						}
					});
					this.$Fetch({
						url: 'user_chat/getNoRead',
						success: res => {
							let uList = this.uList;
							for (let item of res) {
								for (let i in uList) {
									if (item.user_id === uList[i].id) {
										let user = uList[i];
										user.content = item.content;
										user.count = item.count;
										uList[i] = user;
									}
								}
							}
							this.updateUlist(uList);
						}
					});
				}
			});
		}
	},
	onReachBottom() {
		this.getList();
	},
	methods: {
		...mapMutations(['setInfo', 'updateUlist', 'setMsgNum', 'setCount', 'setShareMsg']),
		...mapActions(['navFilter']),
		getList() {
			if (this.loading) {
				this.loading = false;
				this.$Fetch({
					url: 'news/recommend',
					data: {
						pageNum: this.pageNum,
						pageSize: this.pageSize
					},
					success: res => {
						let arr = res.lists;
						if (arr.length > 0) {
							this.list = this.list.concat(arr);
							if (arr.length === this.pageSize) {
								this.pageNum++;
								this.loading = true;
								this.more = true;
							} else {
								this.more = false;
							}
						} else {
							this.more = false;
						}
					}
				});
			}
		}
	}
};
